import { Controller, Commands } from '@ali/4ever-cangjie';
import { Paragraph } from '@ali/4ever-plugin-paragraph';
import RefBlock, { RefBlockData } from '../../mo/models';

/**
 * 插入引用块
 * @param controller
 * @param data
 * @returns
 */
export function insertRefBlock(
  controller: Controller,
  data: RefBlockData,
) {
  const refblock = RefBlock.create({
    data,
    nodes: [Paragraph.create()],
  });

  controller.dispatch('createInsertBlockWithoutExtraBlankAction', { node: refblock });

  return controller
    .command(Commands.moveToEndOfNode, refblock)
    .command(Commands.focus);
}
